.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


body {
  background-color: #2c3e50;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  width: 300px;
  height: 400px;
  background-color: #e67e22;
  display: flex;
  flex-wrap: wrap;
  border-radius: 5px;
}
button {
  height: 50px;
  width: 50px;
  border-radius: 5px;
  margin: 5px;
  padding: 5px;
  font-size: 15px;
  cursor: pointer;
}
.numbers {
  width: 75%;
}
.modifiers {
  width: 25%;
}
.show-input {
  padding: 15px;
  background-color: white;
}